*{
    padding: 0;
    margin: 0;
}

body{
    background-color: #ecf0f1;
}

section{
    width: 100%;
    min-height: 100vh;
    display: flex;
}

section > *{
    margin: auto;
}

.canvas canvas{
    position: absolute;
    top: 0;
    left: 0;

    opacity: 0;
    width: 1280px;
    height: 960px;
}

.canvas, .graph{
    padding: 10px;

    overflow: hidden;

    background-color: #161719;
    background-size: cover;

    width: calc(1280px/2);
    min-height: calc(960px/2);

    border-radius: 5px;
    position: relative;

    margin: 20px;

    transition: all, ease-out, 0.18s;

    cursor: pointer;
}

.canvas{
    margin-bottom: 8px;
}


.graph{
    min-height: calc(960px/3);
}

.canvas:hover, .graph:hover{
    /* -webkit-box-shadow: 0px 0px 15px 2px rgba(0,0,0,0.59); 
    box-shadow: 0px 0px 15px 2px rgba(0,0,0,0.59); */
}

.canvas-input{
    opacity: 0;
    top: -100px;
}

.canvas-add-image{
    color: #fff;
    font-weight: lighter;
    cursor: pointer;

    bottom: 50%;
    left: 50%;
    transform: translate(-50%, 50%);
    
    position: absolute;
}

.equa{
    color: #fff;
    font-weight: lighter;
    font-size: 18px;

    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.apple-shadow{
    -webkit-box-shadow: 0 5px 28px 0 rgba(0,0,0,.1);
    box-shadow: 0 5px 28px 0 rgba(0,0,0,.1);
}

.section-name{
    font-size: 3rem;
    font-weight: bold;
    margin-bottom: 25px;
}

.graph-wrapper{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.image-selector{
    padding: 0 20px 0 20px;

    width: calc(1280px/2 + 20px);
    
    overflow: hidden;

    display: flex;
    
    justify-content: space-around;
}

.image-selector__item{
    width: 24%;
    height: 152px;
    display: inline-flex;
    cursor: pointer;

    flex-direction: column;
    align-items: center;
}

.image-selector__item img{
    width: 100%;
    border-radius: 4px;
    margin-bottom: 10px;
}

.image-selector__item-name{
    font-family: Helvetica, sans-serif;
    font-size: 20px;
    font-weight: 400;
    color: #333;
    white-space: nowrap;
}